
body, html {
  height: 100%;
  width :100%;
  margin: 0;
  padding:0;
}
body {
  background: #ffffff;
  color: #333333;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 13px;
  font-weight: 400;
  letter-spacing: 0.015em;
  line-height: 1.5;
  margin: 0;
}
* {
  box-sizing: border-box;
}
ul, li {
  list-style: none;
  margin: 0;
  padding:0;
}
#app {
  height: 100%;
}
.main-content {
  background-color: #fff;
  height: 100%;
  transition: transform 0.2s ease-in-out;
}
.main-content.with-menu {
  transform: translateX(-330px);
}



